.pad_top_80 {
    padding-top: 30px;
}


.countdown__item, .corner-box {
    border: 2px solid #EE821F;
}

.countdown__item.active, .countdown__item:hover,
.corner-box.active,
.corner-box:hover {
  border-color: #00BFFF;
}

.tf-button .boder-fade {
    border: 2px solid #EE821F;
}
.warning_mint {
    line-height: 1.6;
    color: #fff;
    text-transform: uppercase;
    font-family: "IBM Plex Mono", monospace;
    margin-bottom: 8px;
    letter-spacing: 0.6px;
}
.conn_btn {
    margin-right: 0;
}

@media (min-width: 375px) {
    .conn_btn {
        margin-right: 0 !important;
    }
}